import React from "react";
import "../styles/about.css";
import aboutBackground from "../assets/aboutBackground.jpg";
import { motion } from "framer-motion";

const pageTransitionVariants = {
  initial: {
    width: 0,
  },
  animate: {
    width: "100%",
  },
  exit: {
    x: window.innerWidth,
    transition: {
      duration: 0.1,
    },
  },
};

const textFromLeftFastVariants = {
  initial: { x: "-200%", opacity: 0 },
  animate: { x: 0, opacity: 1, transition: { duration: 0.8 } },
};

const textFromLeftSlowVariants = {
  initial: { x: "-200%", opacity: 0 },
  animate: {
    x: 0,
    opacity: 1,
    transition: { duration: 1.2 },
  },
};

function About() {
  return (
    <motion.div
      className="about"
      variants={pageTransitionVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <div
        className="about-top"
        style={{ backgroundImage: `url(${aboutBackground})` }}
      ></div>
      <div className="about-bottom">
        <motion.h1
          variants={textFromLeftFastVariants}
          initial="initial"
          animate="animate"
        >
          About Us
        </motion.h1>
        <motion.p
          variants={textFromLeftSlowVariants}
          initial="initial"
          animate="animate"
        >
          It is raining outside, and Pete is feeling upset because now he can't
          play ball with his friends. His father comes up with a creative way to
          cheer him up - by turning him into a pizza!
        </motion.p>
      </div>
    </motion.div>
  );
}

export default About;
