import React from "react";
import "../styles/contact.css";
import contactBackground from "../assets/contact.jpg";
import { motion } from "framer-motion";

const pageTransitionVariants = {
  initial: {
    width: 0,
  },
  animate: {
    width: "100%",
  },
  exit: {
    x: window.innerWidth,
    transition: {
      duration: 0.1,
    },
  },
};

const textFromRightFastVariants = {
  initial: { x: "200%", opacity: 0 },
  animate: { x: 0, opacity: 1, transition: { duration: 0.8 } },
};

const textFromRightSlowVariants = {
  initial: { x: "200%", opacity: 0 },
  animate: {
    x: 0,
    opacity: 1,
    transition: { duration: 1.2 },
  },
};

function Contact() {
  return (
    <motion.div
      className="contact"
      variants={pageTransitionVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <div
        className="left-side"
        style={{ backgroundImage: `url(${contactBackground})` }}
      ></div>
      <div className="contact-right-side">
        <motion.h1
          variants={textFromRightFastVariants}
          initial="initial"
          animate="animate"
          className="contact-title"
        >
          Contact us
        </motion.h1>
        <motion.form
          className="form-container"
          method="post"
          variants={textFromRightSlowVariants}
          initial="initial"
          animate="animate"
        >
          <label htmlFor="name">Full Name</label>
          <input type="text" name="name" placeholder="Full name..." />
          <label htmlFor="email">Email</label>
          <input type="email" name="email" placeholder="Full name..." />
          <label htmlFor="message">Message</label>
          <textarea placeholder="Message..." name="message"></textarea>
          <button type="submit">SUBMIT</button>
        </motion.form>
      </div>
    </motion.div>
  );
}

export default Contact;
