import React from "react";
import MenuItem from "../components/MenuItem";
import "../styles/menu.css";
import { MenuList } from "../helpers/MenuList";
import { motion } from "framer-motion";

const pageTransitionVariants = {
  initial: {
    width: 0,
  },
  animate: {
    width: "100%",
  },
  exit: {
    x: window.innerWidth,
    transition: {
      duration: 0.1,
    },
  },
};

const textFromLeftFastVariants = {
  initial: { x: "-200%", opacity: 0 },
  animate: { x: 0, opacity: 1, transition: { duration: 0.8 } },
};

function Menu() {
  return (
    <motion.div
      className="menu"
      variants={pageTransitionVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <motion.h1
        className="menu-title"
        variants={textFromLeftFastVariants}
        initial="initial"
        animate="animate"
      >
        Our Menu
      </motion.h1>
      <div className="menu-items">
        {MenuList.map((item, key) => {
          return (
            <MenuItem
              key={key}
              image={item.image}
              name={item.name}
              price={item.price}
            />
          );
        })}
      </div>
    </motion.div>
  );
}

export default Menu;
