import React from "react";
import { Link } from "react-router-dom";
import "../styles/home.css";
import HeroboxPizza from "../assets/homeBackground.jpg";
import { motion } from "framer-motion";

const pageTransitionVariants = {
  initial: {
    width: 0,
  },
  animate: {
    width: "100%",
  },
  exit: {
    x: window.innerWidth,
    transition: {
      duration: 0.1,
    },
  },
};

const textFromLeftFastVariants = {
  initial: { x: "-200%", opacity: 0 },
  animate: { x: 0, opacity: 1, transition: { duration: 0.8 } },
};

const textFromLeftSlowVariants = {
  initial: { x: "-200%", opacity: 0 },
  animate: {
    x: 0,
    opacity: 1,
    transition: { duration: 1.2 },
  },
};

const elementScaleVariants = {
  initial: { scale: 0, opacity: 0 },
  animate: { scale: 1, opacity: 1, transition: { duration: 1 } },
};

function Home() {
  return (
    <motion.div
      className="home"
      style={{ backgroundImage: `url(${HeroboxPizza})` }}
      variants={pageTransitionVariants}
      initial="initial"
      animate="animate"
      exit="exit"
    >
      <div className="header-container">
        <motion.h1
          variants={textFromLeftFastVariants}
          initial="initial"
          animate="animate"
        >
          DANNI'S PIZZA
        </motion.h1>
        <motion.p
          variants={textFromLeftSlowVariants}
          initial="initial"
          animate="animate"
        >
          Do good. Be nice. Order pizza. <span>Repeat.</span>
        </motion.p>
        <Link to="/menu">
          <motion.button
            variants={elementScaleVariants}
            initial="initial"
            animate="animate"
          >
            ORDER NOW
          </motion.button>
        </Link>
      </div>
    </motion.div>
  );
}

export default Home;
